*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.app {
    height: 100vh;
    &::-webkit-scrollbar{
        display: none;
    }
    // overflow: hidden;
  
    .sections {
        position: relative;
        height: calc(100vh - 70px);
        top: 70px;
        width: 100%;
        overflow-y: scroll;
        scroll-behavior: smooth;
        scroll-snap-type: y mandatory;
       
        > * {
            scroll-snap-align: start;
            width: 100%;
            height: calc(100vh - 70px);
            ;
        }
    }
}