@import "./../../global.scss";
@import url('https://fonts.googleapis.com/css2?family=Anek+Latin:wght@200&display=swap');
.Topbar {
    width: 100%;
    height: 70px;
    background-color: $main-color;
    position: fixed;
    z-index: 3;
    transition: all 1s ease;
    top: 0px;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 30px;
    .topic{
        font-family: 'Anek Latin', sans-serif;
        font-weight: 700;
        font-size: 25px;
    }
    .wrapper {
        width: 100%;
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .left {
            display: flex;
            align-items: center;

            .logo {
                font-size: 18px;
                font-weight: 700;
                text-decoration: none;
                color: inherit;
                margin-right: 40px;
            }

            .itemContainer {
                display: flex;
                align-items: center;
                margin-left: 30px;
                @include mobile{
                    display:  none;
                }
                .icon {
                    font-size: 18px;
                    font-weight: 500;
                }

                a {
                    font-size: 15px;
                    font-weight: 500;
                    text-decoration: none;
                    color: inherit;
                    // z-index: 4;
                }
                span{
                    font-size: 15px;
                    font-weight: 500;
                    text-decoration: none;
                    color: inherit;   
                }
            }
        }

        .right {

            // position: absolute;
            // left: 10px;
            .hamburger {
                width: 32px;
                height: 25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;
                overflow: hidden;
                &::-webkit-scrollbar{
                    display: none;
                }
                 span {
                    width: 100%;
                    height: 3px;
                    background-color: white;
                    transform-origin: left;
                    transition: all 2s ease;
                   
                    // z-index: 4;

                }
            }
        }

    }

    &.active {
        background-color: $side-color;

        .hamburger {
            span {

                &:first-child {
                    transform: rotate(45deg);
                    // z-index: 4;
                }

                &:nth-child(2) {
                    opacity: 0
                    // background-color: black;
                    // display: none;
                }

                &:last-child {
                    transform: rotate(-45deg);
                    // z-index: 4;
                }
            }
        }
    }
}