@import "./../../global.scss";
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@300&family=Roboto:ital,wght@1,100&display=swap');
.Menu {
    width: 300px;
    height: 100vh;
    background-color: $side-color;
    position: fixed;
    top: 0;
    right: -300px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 1s ease;
  @include mobile{
   width: 200px ;
  }
    &.active {
        right: 0px
    }

    ul {
        margin: 0;
        padding: 0;
        list-style: none;
        font-size: 30px;
        font-family: 'Noto Sans', sans-serif;
        font-weight: 300;
        color: white;
        width: 60%;
        margin: auto;
        @include mobile{
            font-size: 20px;
            width:80%;
            margin: auto;
            text-align: center;
        }

        li {
            margin-bottom: 20px;

            a {
                font-size: inherit;
                color: inherit;
                text-decoration: none;
            }

            &:hover {
                font-weight: 700;
            }
        }
    }


}