@import "../../global.scss";
@import url('https://fonts.googleapis.com/css2?family=Uchen&display=swap');
.Intro {
    background-color: white;
    // .pos{
    //     position: absolute;
    //     left: 50%;
    // }
    .wrapper {
        width: 100%;
        display: flex;

        .left {
            @include mobile{
             position: absolute;
             display: none;
             flex: 0;
            }
            flex: 0.5;
            background-color: #001219;
            border-radius: 2% 25% 25% 2%;
        }

        .right {
            @include mobile{
                width: 100%;
                border-radius: 25% 25% 25% 25%;
                flex: 1;
                margin-top: 20vh;
            }
            background-color: #e9d8a6;
            border-radius: 25% 2% 2% 25%;
            flex: 0.5;
            font-family: 'Uchen', serif;
            h1{
                text-align: center;
                margin-top: 15%;
            }
            .content{
                margin: 10% 10%;
                padding: 5%;
            }
        }
    }
}