@import "./../../global.scss";
$h1: calc(100vh - 140px);
$h2: calc(100vh - 143px);
$m1: calc((100vh - 120px)/2);

.Projects {
    background-color: #bb3e03;
    width: 100%;

    h1 {
        overflow: hidden;
        font-size: 30px;
        height: 70px;
        text-align: center;
    }

    .wrapper {
        button {
            border-radius: 3%;
            width: 100%;
            height: 100%;
            font-size: 23px;
           
            @include mobile {
                width: 70%;
                height: 70%;
                margin: 15%;
            }

            background-color: black;
           a{
            color: white;
            text-decoration: none;
           }
            // font-size: 12px;
            margin: auto;
            padding-left: 0.7;
        }

        @include mobile {
            height: $h1;
        }

        display: flex;

        @include mobile {
            flex-direction: column;
        }

        .left {
            overflow: hidden;

            @include mobile {
                height: $h1;
            }

            flex: 0.5;
            height: $h1 ;

            .top {
                height: 50%;

                @include mobile {
                    height: 100%;
                }
            }
        }

        .right {
            overflow: hidden;

            @include mobile {
                height: auto;
            }

            flex: 0.5;
            height: $h2;

            //  align-content: flex-end;
            .bottom {
                margin-top: $m1 ;
                height: 50%;

                @include mobile {
                    height: 100%;
                    margin-top: 0;
                }
            }
        }
    }

}