@import "./../../global.scss";
.Intro {
    background-color: white;
    width: 100%;
    display: flex;
    @include mobile {
        flex-direction: column;
        align-items: center;
    }
    .left {
        flex: 0.5;
        overflow: hidden;

        .imageContainer {
            width: 600px;
            height: 600px;
            border-radius: 26%;
            background-color: green;
            display: flex;
            justify-content: center;
            align-items: center;
            float: right;
            @include mobile{
                align-items: flex-start;
            }
            img {
                border-radius: 50%;
                width: 70%;
                height: 90%;
                @include mobile{
                    height: 50%;
                    width: 40%;
                }
            }
        }
    }

    .right {
        flex: 0.5;

        .wrapper {
            height: 100%;
            width: 100%;
            padding-left: 50px;
            padding-top: 50px;
            display: flex;
            flex-direction: column;
            @include mobile{
                padding-left: 0;
            }
            h1 {
                font-size: 65px;
                @include mobile{
                    font-size: 34px;
                }
            }

            h2 {
                font-size: 39px;
                @include mobile{
                    font-size: 26px;
                }
            }

            h3 {
                font-size: 22px;
                color: red;
                @include mobile{
                    font-size: 23px;
                }
            }
        }

        a {
            position: absolute;
            bottom: 10px;
            left: 55%;
        }
        img{
            width: 30px;
            animation: arrowBlink 2s infinite;
        }

        @keyframes arrowBlink{
            100%{
                opacity: 0;
            }
            100%{
                opacity: 0;
            }
        }

    }
}