@import "./../../global.scss";
.Contact {
    width: 100%;
    height: calc(100vh-70px);
    .wrapper {
        display: flex;
        width: 100%;
        height: 100%;        

        .left {
            overflow: hidden;
            flex: 0.5;
            display: flex;
            background-color: #94d2bd;
            flex-direction: column;
           height: 100%;
            align-items: center;
            .center{
                flex: 1;
                // margin-top: calc(100vh - 60px);
                height: 100%;
                width: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                @include mobile{
                    overflow: visible;
                    margin-bottom: 7vh;
                    height: 0;
                    flex: 1;
                    align-items: flex-end;
                    margin-bottom: 10vh;
                }
            }
            .bottom{
                flex: 1;
                justify-content: center;
                width: 100%;
                display: flex;
                flex-direction: row;
                @include mobile{
                    height: 0;
                    flex: 2;
                    flex-direction: column;
                    justify-content: space-around;
                }
                justify-content: space-around;
                align-items: center;

            }
            .icon {
                display: flex;
                justify-content: center;
                align-items: center;
                height: 100px;
                background-color: white;
                border-radius: 50%;
                width: 100px;
                box-shadow: 5px 10px #3a5254;
                transition: all 0.8s ease;
            }

            .icon:hover {
                box-shadow: 5px -10px #3a5254;
            }
        }

        .right {
            flex: 0.5;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            background-color: #ca6702;

            h2 {
                font-size: 30px;
            }

            form {
                width: 70%;
                height: 70%;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;

                input {
                    width: 300px;
                    height: 30px;
                    font-size: 14px;
                    margin-bottom: 10px;
                    border-radius: 12px;
                    box-shadow: 0 0 0 3px #fff;
                    border: 5px solid transparent;
                    outline: none;
                    @include mobile{
                        margin-bottom: 15px;
                        width: 90%;
                    }

                }

                textarea {
                    width: 300px;
                    height: 200px;
                    font-size: 14px;
                    box-shadow: 0 0 0 3px #fff;
                    border: 5px solid transparent;
                    border-radius: 12px;
                    outline: none;
                    margin-bottom: 10px;
                    
                    @include mobile{
                        margin-bottom: 15px;
                        width: 90%;
                    }
                }

                button {
                    width: 150px;
                    height: 30px;
                    color: white;
                    background-color: crimson;
                    border: none;
                    border-radius: 10px;
                    font-weight: 500;
                    cursor: pointer;
                    
                }
            }
        }
    }
}